import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Button, Row, Col, Container } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>CONTENT RATINGS</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range. These content ratings are based on those of the Canadian
          Home Video Rating System (CHVRS).
          Click the button below for detailed content rating guidelines.
        </p>

        <Button href="https://docs.google.com/document/d/1mNsA7U5pP_rh_4VNz2k7bnlRoFF89fExP2qTK_ZMR14/edit?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton'>
          CONTENT RATING GUIDELINES
        </Button><br /><br />

        <Container>
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/1-G.png')} alt='G' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>GENERAL</u></h3>
              <p className='ratingSummary'>
                Generally suitable for all ages. <br />
                Although not necessarily aimed for young children, content classified G should not unsettle most children
                5 - 7 years old. Parental guidance for children below that age range is still recommended.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/2-PG.png')} alt='PG' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>PARENTAL GUIDANCE</u></h3>
              <p className='ratingSummary'>
                Parental guidance is advised for younger children, especially under ages 10 - 13. <br />
                PG content may include more serious or grown-up perspectives on issues that may be too complicated or
                distressing for younger or more sensitive children.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/3-14+.png')} alt='14+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 14+</u></h3>
              <p className='ratingSummary'>
                Not recommended for minors under 14. <br />
                Mature themes become more prevalent beginning from 14+, so content may be
                too inappropriate for children who are still in their early adolescence.
              </p>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={3} xl={2}><img src={require('./content_ratings/4-18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col xs={12} md={9}>
              <h3><u>AGES 18+</u></h3>
              <p className='ratingSummary'>
                Not recommended for minors under 18. <br />
                Treatment of themes and other material in an 18+ content can be quite mature and thus may risk harm to
                teenagers' sense of wellbeing, safety, or ethics (especially on the Internet). 18+ may also include
                extreme or explicit material that may disturb adults of any age. <br />
                <strong><em>No minors under 18 are allowed to access content classified 18+.</em></strong>
              </p>
            </Col>
          </Row><br />
        </Container>

      </animated.div>
    </div>
  );
}

export default ContentRatings;
